import { Theme } from "src/themes/light"
import { css, g, React } from "src/utils/view"

import SiteLink from "./Link"
import { Random } from "./Random"
import ThemeSwitcher from './ThemeSwitcher'

type Props = {
  siteTitle: string
}

const Header = ({ siteTitle }: Props) => (
  <div
    style={{
      marginBottom: "1.45rem",
    }}
  >
    <div
      style={{
        margin: "0 auto",
        maxWidth: 960,
        padding: "1.45rem 1.0875rem",
        transform: "skewY(-11deg)",
        overflow: "hidden"
      }}
    >
      <ThemeSwitcher />
      <SiteLink to="/">
        <SiteHeader>
          {siteTitle}
        </SiteHeader>
      </SiteLink>
      <Slogan>
        <Random>
          <span>engineer</span>
          <span>software engineer</span>
          <span>sre</span>
          <span>open source developer</span>
          <span>developer</span>
        </Random>
      </Slogan>
      <NavBar>
        <SiteLink to="/posts">
          <NavBarButton>posts</NavBarButton>
        </SiteLink>
        <SiteLink to="/slides">
          <NavBarButton>slides</NavBarButton>
        </SiteLink>
        <SiteLink to="/about">
          <NavBarButton>about</NavBarButton>
        </SiteLink>
      </NavBar>
    </div>
  </div>
)

type ThemeProp = {
  theme: Theme
}

type OptionalTheme = { theme?: Theme | undefined }

const SiteHeader = g.h1<OptionalTheme>`
  margin: 0;
  margin-left: -100;
  padding-left: 100;
  width: 150%;
  color: ${({ theme }: ThemeProp) => theme.header.color};
  filter: ${({ theme }: ThemeProp) => theme.header.filter};
  background: ${({ theme }: ThemeProp) => theme.header.background};
`

const NavBar = g("div")({
  display: "flex",
  justifyContent: "space-evenly",
})

const NavBarButton = g("h3")({
  "> a": {
    paddingTop: 5,
  },
})

const Slogan = g("h6")<OptionalTheme>(({ theme }: ThemeProp) => css`
  color: ${theme.slogan.color};
  margin: 0;
`);

export default Header
