import { useContext } from "react";
import { ThemeNameContext, themes } from "src/context/ThemeContext"
import { React, g } from "src/utils/view"

export default () => {
  const { themeName, setThemeName } = useContext(ThemeNameContext);
  return <Container role="button" onClick={() => setThemeName(themeName === 'dark' ? 'light' : 'dark')}>{themes[themeName].switcher}</Container>
}

const Container = g.span`
  cursor: pointer;
`
