import { Global, withTheme } from "@emotion/react"
import { useContext, useMemo } from "react"
import { Theme } from 'src/themes/light'
import { ThemeNameContext, themes } from "src/context/ThemeContext"
import { css, keyframes, React } from 'src/utils/view'

type Props = {
  theme: Theme
}

const getChangeThemeAnimation = (newTheme: Theme) => {
  const { previousThemeName } = useContext(ThemeNameContext);
  const previousTheme = themes[previousThemeName];
  return keyframes`
    from {
      background: ${previousTheme.background};
    }
    to {
      background: ${newTheme.background};
    }
  `
}

const fonts = css`
  @font-face {
    font-family: "Open Sans Extra Bold";
    src: url(/fonts/open-sans/OpenSans-ExtraBold.ttf);
  }
  @font-face {
    font-family: "Cooper Hewitt";
    src: url(/fonts/cooper-hewitt/CooperHewitt-Light.otf);
  }
  @font-face {
    font-family: "PT Sans";
    src: url(/fonts/pt-sans/PTN57F.ttf);
  }
`

const GlobalStylesComponent = ({ theme }: Props) => {
  const styles = useMemo(() => getStyles(theme), [theme]);
  return <>
    <Global styles={fonts} />
    <Global styles={styles} />
  </>;
}

// const dark = {
//   backgroundColor: 'black',
// }
// const light = {
//   backgroundColor: 'white',
// }

export const getStyles = (theme: Theme) => css`
      code {
        font-family: ${theme.code.fontFamily};
        background-color: ${theme.code.backgroundColor};
      }

      html,
      p {
        line-height: 1.8em;
      }
      
      body {
        font-family: ${theme.fontFamily};
        margin: 2%;
        font-size: 14pt;
        color: ${theme.color};
        background: ${theme.background};
        animation: ${getChangeThemeAnimation(theme)} 1s ease 1;
      }
      
      h1 {
        letter-spacing: -1pt;
        line-height: 1.2em;
        font-family: "Open Sans Extra Bold", "Lucida Grande", "Lucida Sans",
          "Lucida Sans Regular", "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
      }
      h2,
      h3,
      h4,
      h5,
      h6 {
        letter-spacing: 1.3pt;
        font-family: "Cooper Hewitt";
        text-transform: uppercase;
      }
      @media only screen and (max-width: 480px) {
        html {
          font-size: 100%;
        }
      }
    `

export const GlobalStyles = withTheme(GlobalStylesComponent)
