import GatsbyLink from "gatsby-link"
import { OutboundLink } from "gatsby-plugin-google-analytics"
import { Theme } from "src/themes/light"
import { g, React } from "src/utils/view"

type Props = {
  href?: string
  to?: string
  newTab?: boolean
  children: React.ReactNode
}

export default function SiteLink({ href, to, children, newTab }: Props) {
  const finalDestination = to || href || "/"
  const isText =
    typeof children === "string" ||
    (typeof children === "object" && (children as string[]).length > 0)
  if (finalDestination.substr(0, 1) === "/" && newTab !== true) {
    const StyledLink = isText ? StyledTextLink : StyledNonTextLink
    return (
      <StyledLink activeStyle={{ color: "red" }} to={finalDestination}>
        {children}
      </StyledLink>
    )
  } else {
    const ExternalLink = isText ? ExternalTextLink : ExternalNonTextLink
    return (
      <ExternalLink href={finalDestination} target={"_blank"}>
        {children}
      </ExternalLink>
    )
  }
}

type ThemeProp = {
  theme: Theme
}

const ExternalTextLink = g(OutboundLink)`
background-image: ${({ theme }: ThemeProp) => theme.link.backgroundImage};
transition:
  background-position 120ms ease-in-out, padding 120ms ease-in-out;
background-size: 100% 200%;
background-position: 0 0;
text-decoration: none;
color: ${({ theme }: ThemeProp) => theme.link.normal};
padding: 0;
:hover {
  background-image: ${({ theme }: ThemeProp) =>
    theme.link.hoverBackgroundImage};
  background-position: 0 90%;
}
`

const ExternalNonTextLink = g(OutboundLink)`
  text-decoration: none;
  color: ${({ theme }: ThemeProp) => theme.link.normal};
  :hover {
    color: ${({ theme }: ThemeProp) => theme.link.hover};
    padding: 0;
    outline: 1px solid gray;
    outline-offset: 2;
  }
  :focus {
    outline: 1px dashed gray;
    outline-offset: 2;
  }
  :visited {
    color: ${({ theme }: ThemeProp) => theme.link.visited};
    outline-offset: 2;
  }
`

type OptionalTheme = { theme?: Theme | undefined }

const StyledTextLink = g(GatsbyLink)<OptionalTheme>`
  background-image: ${({ theme }: ThemeProp) => theme.link.backgroundImage};
  transition:
    background-position 120ms ease-in-out, padding 120ms ease-in-out;
  background-size: 100% 200%;
  background-position: 0 0;
  text-decoration: none;
  color: ${({ theme }: ThemeProp) => theme.link.normal};
  padding: 0;
  :hover {
    background-image: ${({ theme }: ThemeProp) =>
      theme.link.hoverBackgroundImage};
    background-position: 0 90%;
  }
`

const StyledNonTextLink = g(GatsbyLink)<OptionalTheme>`
  text-decoration: none;
  color: ${({ theme }: ThemeProp) => theme.link.normal};
  :hover {
    color: ${({ theme }: ThemeProp) => theme.link.hover};
    padding: 0;
    outline: 1px solid gray;
    outline-offset: 2;
  }
  :focus {
    outline: 1px dashed gray;
    outline-offset: 2;
  }
  :visited {
    color: ${({ theme }: ThemeProp) => theme.link.visited};
    outline-offset: 2;
  }
`
